import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";
import moment from "moment";
import { Prop } from "vue-property-decorator";
import prescriptioninfo from "../transmit/prescriptioninfo";
import updateorder from "./updateorder";

@Component({
  props: {},
  components: {
    prescriptioninfo,
    updateorder
  }
})
export default class AddGoods extends Vue {
  @Prop({ required: false })
  orderList: any;
  @Prop({ required: false })
  pagetype: string;
  @Prop({ required: false })
  patModel: string;
  @Prop({ required: false })
  provinceList;
  @Prop({ required: false })
payStatus
handleWidth(){
if(this.pagetype=='reminder'){
  return "290"
}
return "220"
}


handleInvoinStatus(row){
if(row.invoiceRecords && row.invoiceRecords.length>0&& row.invoiceRecords[0].shipStatus == 'HAVE_SIGNED_IN'){
  return {title:"开票完成",type:'success'}
}else if(row.isInvoicedShipped == '1'){
  return {title:"已开发票",type:'warning'}
}else if(row.isInvoiced == '1'){
  return {title:"新增开票",type:'danger'}
}else if(row.isInvoiced == '0'){
  return {title:"未开发票",type:'info'}
}else{
  return {title:"",type:''}
}
}


sendGoods(row){
    let a: any = this.$refs.updateorder;
    a.send_model = true;
        a.loading = true
    this.presId = row.presId
        sessionStorage.presId = row.presId
     this.getOrderDetail(row.presId);
     
}


  invoiceRecordsObj = {};
  invoiceRecordModel = false;
  getInvoiceRecords(index, row) {
    this.invoiceRecordsObj = row.invoiceRecords;
  }
  giveupModel = false;
  presId = "";
  reason = "";
  doGiveup(row) {
    this.presId = row.presId;
    this.reason = "";
    this.giveupModel = !this.giveupModel;
  }
  doGiveupSubmit() {
    this.$confirm("是否对该订单弃单?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        if ((this.reason || "") == "") {
          this.$message("请输入备注原因");
          return;
        }
        indexApi
          .doGiveup({
            preId: this.presId,
            reason: this.reason
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("已弃单");
              this.$emit("getOrderList");
              this.giveupModel = !this.giveupModel;
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
              console.error("数据查询错误");
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }
  order = {};
  doReminder(row) {
    // this.$confirm("确定要发送一次催单?", "提示", {
    //   confirmButtonText: "确定",
    //   cancelButtonText: "取消",
    //   type: "warning"
    // })
    //   .then(() => {
    indexApi
      .changeReminderStatus({
        presId: row.presId
      })
      .then(res => {
        if (res["retCode"]) {
          this.$message("已催单");
          this.$emit("getOrderList");
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
    // })
    // .catch(() => {
    //   this.$message({
    //     type: "info",
    //     message: "已取消操作"
    //   });
    // });
  }

  getorderInfo(row) {
    let a: any = this.$refs.updateorder;
    a.model = true;
    a.loading = true
    this.presId = row.presId
    this.getOrderDetail(row.presId);

  }

  doRework(index,row){
      let a: any = this.$refs.updateorder;
    a.reworkModel = true;
    a.loading = true
        this.presId = row.presId
    this.getOrderDetail(row.presId);
  }

  
  handlePaymentMode(status) {
    switch (status) {
      case "ONLINE_PAYMENT":
        return {type:'success',title:"微信支付"};
      case "ORDER_PAY_ONDEV":
        return {type:'warning',title:"货到付款"};
      default:
        return {type:'',title:""};
    }
  }

  getOrderDetail(presId) {
    indexApi
      .getOrderInfo({
        presId: presId
      })
      .then(res => {
        if (res["retCode"]) {
          res.data.YdkOrderDetailList.forEach((item, index) => {
            item.logisticslabel = item.logistics;
            item.waybillNumberlabel = item.waybillNumber;
          });
          if (res.data.invoiceRecords && res.data.invoiceRecords.length > 0) {
            res.data.invoiceObj_logistics =
              res.data.invoiceRecords[0].logistics;
            res.data.invoiceObj_waybillNumber =
              res.data.invoiceRecords[0].waybillNumber;
          }
          this.order = res.data;
          (<any>this.$refs.updateorder).getExpressPackage(presId);
        } else {
               (<any>this.$refs.updateorder).loading = false
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
        }
      });
  }

  updateOrder = {};


  getOrderList() {
    this.$emit("getOrderList");
  }
  handlePayStatus(status) {
    switch (status) {
      case "PAY_WAIT":
        return "等待支付";
      case "PAY_SUCCESS":
        return "支付成功";
      default:
        return "";
    }
  }

  prescriptioninfoObj = {};
  preImageList = [];
  getTransmitInfo(index, row) {
    this.prescriptioninfoObj = row;
    let a: any = this.$refs.prescriptioninfo;
    a.getInfo();
  }

  handleOrderType(row){
  if( row.splitFlag==='1'){
    return "拆分订单"
  }
   if( row.splitFlag==='0'){
    return "普通订单"
  }
  return ""
  }
  handleOrderStatus(status) {
    switch (status) {
      case "ORDER_INIT":
        return {
          type:"info",
          title:"订单初始化"
          };
      case "ORDER_WAIT_PAY":
        return {
          type:"",
          title:"等待支付"
          };
      case "ORDER_PAY_ONDEV":
        return  {
          type:"warning",
          title:"货到付款"
          };
      case "ORDER_CANCEL_PAY":
        return {
          type:"info",
          title:"取消支付"
          };
      case "ORDER_WAIT_SENDGOODS":
        return {
          type:"danger",
          title:"待发货"
          }
      case "SENDGOODS_UNFINISHED":
        return   {
          type:"",
          title:"发货未完成"
          }; 
            case "ORDER_WAIT_RECVGOODS":
        return {
          type:"warning",
          title:"待收货"
          }; 
      case "ORDER_END_GOODS":
        return {
          type:"success",
          title:"完成"
          }; 
      default:
        return {  type:"",
          title:""};
    }
  }
  shipList = [];
  queryShipList() {
    indexApi.queryShipList({}).then(res => {
      if (res["retCode"]) {
        this.shipList = res.data.KuaibirdComTrans;
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
        console.error("数据查询错误");
      }
    });
  }

  mounted() {
    this.queryShipList();
  }
}



